___CSS_LOADER_EXPORT___.push([module.id, `/* ClipEditor Component Styles */

.clip-editor {
  background: var(--bg-white);
  border-top: 1px solid var(--border);
  padding: 12px;
  height: 220px;
  overflow-y: auto;
  flex-shrink: 0;
}

.editor-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 160px;
  text-align: center;
  color: var(--text-muted);
  padding: 0 16px;
}

.editor-placeholder .empty-icon {
  color: var(--text-muted);
  margin-bottom: 4px;
}

.editor-placeholder h3 {
  margin: 0 0 4px 0;
  font-size: 18px;
  font-weight: 500;
  color: var(--text);
}

.editor-placeholder .text-muted {
  font-size: 13px;
  color: var(--text-muted);
}

.editor-header {
  margin-bottom: 12px;
  padding-bottom: 8px;
  border-bottom: 1px solid var(--border);
}

.editor-header h3 {
  margin: 0 0 4px 0;
  font-size: 16px;
  font-weight: 600;
  color: var(--text);
}

.clip-name {
  font-size: 14px;
  color: var(--text-muted);
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.trim-controls {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.trim-main-row {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  flex-wrap: wrap;
}

.trim-input-group {
  display: flex;
  flex-direction: column;
  gap: 4px;
  min-width: 0;
}

.trim-input-group label {
  font-size: 12px;
  font-weight: 500;
  color: var(--text);
  flex-shrink: 0;
}

.trim-input-group input {
  padding: 6px 8px;
  border: 1px solid var(--border);
  border-radius: 4px;
  font-size: 13px;
  background: white;
  transition: border-color 0.2s ease;
  width: 70px;
  flex-shrink: 0;
}

.trim-input-group input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.1);
}

.trim-input-group input.error {
  border-color: var(--error);
  box-shadow: 0 0 0 3px rgba(208, 2, 27, 0.1);
}

.validation-error {
  padding: 8px 12px;
  background: rgba(208, 2, 27, 0.1);
  border: 1px solid rgba(208, 2, 27, 0.2);
  border-radius: 6px;
  font-size: 14px;
  color: var(--error);
}

.trim-info-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.trim-info {
  background: rgba(74, 144, 226, 0.05);
  border: 1px solid rgba(74, 144, 226, 0.1);
  border-radius: 4px;
  padding: 6px 8px;
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 12px;
  flex-shrink: 0;
}

.duration-label {
  font-size: 11px;
  color: var(--text-muted);
}

.duration-value {
  font-size: 12px;
  font-weight: 500;
  color: var(--text);
}

.trim-actions {
  display: flex;
  gap: 8px;
  flex-shrink: 0;
  align-self: flex-start;
  margin-top: 20px;
}

.btn {
  padding: 6px 12px;
  border: none;
  border-radius: 6px;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 70px;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-primary {
  background: var(--primary);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background: #357abd;
  transform: translateY(-1px);
}

.btn-secondary {
  background: var(--bg-light);
  color: var(--text);
  border: 1px solid var(--border);
}

.btn-secondary:hover:not(:disabled) {
  background: var(--border);
  transform: translateY(-1px);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .clip-editor {
    padding: 16px;
  }
  
  .trim-main-row {
    flex-direction: column;
    align-items: stretch;
    gap: 12px;
  }
  
  .trim-input-group {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  
  .trim-input-group input {
    width: 80px;
  }
  
  .trim-info-row {
    flex-direction: column;
    align-items: stretch;
    gap: 8px;
  }
  
  .trim-info {
    justify-content: center;
  }
  
  .trim-actions {
    justify-content: center;
  }
}
