___CSS_LOADER_EXPORT___.push([module.id, `/* VideoPreview Component Styles */

.video-preview {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: var(--bg-white);
  border-radius: 8px;
  overflow: hidden;
  box-shadow: var(--shadow-sm);
}

/* Video Container */
.video-container {
  position: relative;
  flex: 1;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 250px;
}

.video-element {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background: #000;
}

/* Loading Overlay */
.video-loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  z-index: 10;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top: 3px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.video-loading p {
  font-size: 14px;
  margin: 0;
}

/* Video Controls */
.video-controls {
  display: flex;
  align-items: center;
  padding: var(--spacing-sm) var(--spacing-md);
  background: var(--bg-white);
  border-top: 1px solid var(--border-light);
  gap: var(--spacing-md);
}

/* Play/Pause Button */
.control-btn {
  background: var(--primary);
  color: white;
  border: none;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.2s ease;
  flex-shrink: 0;
}

.control-btn:hover:not(:disabled) {
  background: var(--primary-dark);
  transform: scale(1.05);
}

.control-btn:active:not(:disabled) {
  transform: scale(0.95);
}

.control-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* Time Display */
.time-display {
  display: flex;
  align-items: center;
  font-family: 'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace;
  font-size: 14px;
  color: var(--text-primary);
  font-weight: 500;
  min-width: 120px;
  flex-shrink: 0;
}

.time-separator {
  margin: 0 4px;
  color: var(--text-secondary);
}

/* Scrubber */
.scrubber-container {
  flex: 1;
  display: flex;
  align-items: center;
  margin: 0 var(--spacing-sm);
}

.scrubber {
  width: 100%;
  height: 6px;
  background: var(--border-light);
  border-radius: 3px;
  outline: none;
  cursor: pointer;
  appearance: none;
  -webkit-appearance: none;
}

.scrubber::-webkit-slider-thumb {
  appearance: none;
  -webkit-appearance: none;
  width: 16px;
  height: 16px;
  background: var(--primary);
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s ease;
}

.scrubber::-webkit-slider-thumb:hover {
  background: var(--primary-dark);
  transform: scale(1.2);
}

.scrubber::-moz-range-thumb {
  width: 16px;
  height: 16px;
  background: var(--primary);
  border-radius: 50%;
  cursor: pointer;
  border: none;
  transition: all 0.2s ease;
}

.scrubber::-moz-range-thumb:hover {
  background: var(--primary-dark);
  transform: scale(1.2);
}

.scrubber:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.scrubber:disabled::-webkit-slider-thumb {
  cursor: not-allowed;
}

.scrubber:disabled::-moz-range-thumb {
  cursor: not-allowed;
}

/* Video Metadata */
.video-metadata {
  padding: var(--spacing-sm) var(--spacing-md);
  background: var(--bg-light);
  border-top: 1px solid var(--border-light);
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
  flex-wrap: wrap;
}

.metadata-item {
  display: flex;
  align-items: center;
  gap: 4px;
  flex-shrink: 0;
}

.metadata-label {
  font-size: 11px;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.3px;
}

.metadata-value {
  font-size: 12px;
  color: var(--text-primary);
  font-weight: 500;
  max-width: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Preview Placeholder (inherited from main.css) */
.video-preview .preview-placeholder {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px;
  text-align: center;
}

.video-preview .preview-placeholder .empty-icon {
  margin-bottom: 20px;
  opacity: 0.3;
  color: var(--text-tertiary);
  display: flex;
  align-items: center;
  justify-content: center;
}

.video-preview .preview-placeholder .empty-icon.error {
  color: var(--error);
  opacity: 0.6;
}

.video-preview .preview-placeholder h2 {
  margin-bottom: 8px;
  color: var(--text-primary);
  font-size: 24px;
}

.video-preview .preview-placeholder p {
  color: var(--text-secondary);
  font-size: 16px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .video-controls {
    padding: var(--spacing-sm);
    gap: var(--spacing-sm);
  }
  
  .control-btn {
    width: 40px;
    height: 40px;
    font-size: 16px;
  }
  
  .time-display {
    font-size: 12px;
    min-width: 100px;
  }
  
  .video-metadata {
    padding: var(--spacing-sm);
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-sm);
  }
  
  .metadata-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 2px;
  }
  
  .metadata-value {
    max-width: none;
  }
}

/* Focus states for accessibility */
.control-btn:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

.scrubber:focus-visible {
  outline: 2px solid var(--primary);
  outline-offset: 2px;
}

/* Animation for smooth transitions */
.video-preview {
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
